        this.setData({
          identity: 1
        });
      } else if (role == "ROLE_SELL_DEPUTY") {
        this.setData({
          identity: 2
        });
      }

      if (userInfo.authorityOther == 7 || role == 'ROLE_SALES_DIRECTOR') {
        this.setData({
          admin: true
        });
      }
