    tapDelete: function () {
      if (!this.admin) {
        return;
      }

      let list = ['删除客户'];
      let id = this.customerInfo.id;
      wx.showActionSheet({
        itemList: list,
        success: function (res) {
          wx.showModal({
            title: '删除客户',
            content: '删除客户后该客户的信息将作废，确定删除该客户？',

            success(res) {
              if (res.confirm) {
                JsyServer.deleteCompany(id).then(res => {
                  let pages = getCurrentPages();
                  let prevPage = pages[pages.length - 2];
                  prevPage.setData({
                    setOver: true
                  });
                  wx.showToast({
                    title: '删除客户成功'
                  });
                  let timer = setTimeout(() => {
                    wx.navigateBack({});
                  }, 1000);
                }).catch(err => {
                  console.log("删除失败err=>", err);
                  wx.showToast({
                    title: "删除客户失败",
                    icon: 'none'
                  });
                });
              } else if (res.cancel) {
                console.log('用户点击取消');
              }
            }

          });
          console.log("111Res---", res);
        }
      });
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
